import { configureStore, combineReducers } from '@reduxjs/toolkit';
import {
  persistStore,
  persistReducer,
  FLUSH,
  REHYDRATE,
  PAUSE,
  PERSIST,
  PURGE,
  REGISTER,
} from 'redux-persist';
import storage from 'redux-persist/lib/storage';
import myFilterSlice from './myFilterSlice';
import { ontactApi } from './myContactsSlice';
// import registerSlice from './registerSlice';
import authSlice from '././auth/authSlice';
// import { registerApi } from './auth/registerSlice';

// export const store = configureStore({
//   reducer: {
//     [ontactApi.reducerPath]: ontactApi.reducer,
//     // [registerApi.reducerPath]: registerApi.reducer,
//     register: registerSlice,

//     filter: myFilterSlice,
//   },
//   middleware: getDefaultMiddleware => [
//     ...getDefaultMiddleware(),
//     ontactApi.middleware,
//     // registerApi.middleware,
//   ],
// });

const rootReducer = combineReducers({
  [ontactApi.reducerPath]: ontactApi.reducer,
  // [authSlice.reducerPath]: authSlice.register,
  filter: myFilterSlice,
  auth: authSlice,
});

const persistConfig = {
  key: 'root',
  storage,
  // blacklist: ['filter'],
  // whitelist: ['token'],
};

const persistedReducer = persistReducer(persistConfig, rootReducer);

export const store = configureStore({
  reducer: persistedReducer,
  middleware: getDefaultMiddleware => [
    ...getDefaultMiddleware({
      serializableCheck: {
        ignoredActions: [FLUSH, REHYDRATE, PAUSE, PERSIST, PURGE, REGISTER],
      },
    }),
    ontactApi.middleware,
  ],

  // middleware: getDefaultMiddleware => [
  //   ...getDefaultMiddleware(),
  //   ontactApi.middleware,
  //   //     // registerApi.middleware,
  // ],
});

export const persistor = persistStore(store);
